import React from "react";
import { MDBContainer, MDBRow, MDBCol, MDBBtn } from "mdbreact";
import Lightbox from "react-image-lightbox";
import '../App.css';

class LightboxPage extends React.Component {
state = {
  photoIndex: 0,
  isOpen: false,
  images: [
    'https://magicmockups.com/media/screen/guest/72/76d530554d7d4c368b4d85eba98921bb_13_960.jpg',
    'https://magicmockups.com/media/screen/guest/89/f99db11c0ae045f0b7fe6f86ceace6ae_25_1280.jpg',
    'https://magicmockups.com/media/screen/guest/04/b4567cfbdef447d5b4c83a90c54e56ea_23_1280.jpg',
    'https://vigroup.ru/upload/medialibrary/f53/MacBook-Pro-mockup.png',
  
  ]
}

renderImages = () => {
  let photoIndex = -1;
  const { images } = this.state;

  return images.map(imageSrc => {
    photoIndex++;
    const privateKey = photoIndex;
    return (
      <MDBCol md="3" key={photoIndex}>
        <figure>
          <img
            src={imageSrc}
            alt="Gallery"
            className="img-fluid z-depth-1"
            onClick={() =>
              this.setState({ photoIndex: privateKey, isOpen: true })
            }
          />
          <p>Project</p>
        </figure>
      </MDBCol>
    );
  })
}

render() {
const { photoIndex, isOpen, images } = this.state;
  return (
      <div className="portfolio">
    
      <h2 class="h1 text-center text-uppercase font-weight-bold pt-5 mb-3 mt-5">Portfolio</h2>
     C#, Java J2EE, JavaScript, SPAs (React.js, Angular 7) MySQL, FireStore, HTML5, CSS3, Bootstrap, Material Design etc. <br />
     Here you can see only client-side presentation layer. For downloading examples, or fully working applications, please, visit my Github (github.com/amirq17)
      <div className="mdb-lightbox p-3">
        <MDBRow>
          {this.renderImages()}
        </MDBRow>
      </div>
      {isOpen && (
        <Lightbox
          mainSrc={images[photoIndex]}
          nextSrc={images[(photoIndex + 1) % images.length]}
          prevSrc={images[(photoIndex + images.length - 1) % images.length]}
          imageTitle={photoIndex + 1 + "/" + images.length}
          onCloseRequest={() => this.setState({ isOpen: false })}
          onMovePrevRequest={() =>
            this.setState({
              photoIndex: (photoIndex + images.length - 1) % images.length
            })
          }
          onMoveNextRequest={() =>
            this.setState({
              photoIndex: (photoIndex + 1) % images.length
            })
          }
        />
      )}
      <MDBCol md="12" className="text-center py-4">
        
      </MDBCol>
    </div>
    );
  }
}

export default LightboxPage;