import React, { Component } from "react";
import '../App.css';
import '../Style.css';
import wxqr from '../assets/wx-qr.jpg';
import {Nav, Navbar, NavbarBrand, Container} from 'react-bootstrap';
import {MDBNavLink, MDBBtn, MDBModal, MDBModalHeader, MDBModalBody, MDBModalFooter} from 'mdbreact';
import logo from '../assets/logo.png';

export default class NavbPage extends Component {

  state = {
    modal: false
  }
  
  toggle = () => {
    this.setState({
      modal: !this.state.modal
    });
  }
render() {

  return (
<Container>
      <Navbar collapseOnSelect expand="lg" bg="bg-black" className="navigbar" variant="dark">
  <Navbar.Brand href="#home">
  
  <img className="logo bounceIn" src={logo} alt="logo"></img>
 
   </Navbar.Brand>
   <div className="social zoomIn bounceIn">
   <img onClick={this.toggle} className="socialimg" width="40px" src="https://image.flaticon.com/icons/svg/906/906359.svg"></img>

  <a href="https://github.com/Amirq17"> <img className="socialimg" width="40px" src=" https://image.flaticon.com/icons/svg/1051/1051275.svg "></img></a>
   </div>
  

  
  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse id="responsive-navbar-nav">
  
    <Nav className="mr-auto menu-center">
  
    <MDBNavLink activeStyle="active" to="/" exact={""}>HOME</MDBNavLink>
    <MDBNavLink activeStyle="active" to="/portfolio">PORTFOLIO</MDBNavLink>
    </Nav>
    <Nav>
      <Nav.Link href="https://docdro.id/uU5me2A">CV *.pdf</Nav.Link>
      
    </Nav>
  </Navbar.Collapse>
</Navbar>
 
  <MDBModal isOpen={this.state.modal} toggle={this.toggle}>
    <MDBModalHeader toggle={this.toggle}>WeChat QR Code</MDBModalHeader>
    <MDBModalBody>
      <img className="wx" src={wxqr} alt="wechat"></img>
    </MDBModalBody>
   
  </MDBModal>

</Container>


 

);
  }
}